import {lazy} from 'react';
import {
    CBadge,
    CButton,
    CButtonGroup,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CProgress,
    CRow,
    CCallout
  } from '@coreui/react'
  import CIcon from '@coreui/icons-react';
  import MainChartExample from 'modules/Charts/MainChartExample.js';
  const WidgetsDropdown = lazy(() => import('modules/Widgets/WidgetsDropdown.js'));
  
const view = (props) => {
    return (
        <>
          <WidgetsDropdown />
          <CCard>
            <CCardBody>
              <CRow>
                <CCol sm="5">
                  <h4 id="traffic" className="card-title mb-0">Statistik Pengunjung</h4>
                  <div className="small text-muted">Januari 2021</div>
                </CCol>
                <CCol sm="7" className="d-none d-md-block">
                  <CButton color="primary" className="float-right">
                    <CIcon name="cil-cloud-download"/>
                  </CButton>
                  <CButtonGroup className="float-right mr-3">
                    {
                      ['Hari', 'Bulan', 'Tahun'].map(value => (
                        <CButton
                          color="outline-secondary"
                          key={value}
                          className="mx-0"
                          active={value === 'Bulan'}
                        >
                          {value}
                        </CButton>
                      ))
                    }
                  </CButtonGroup>
                </CCol>
              </CRow>
              <MainChartExample style={{height: '300px', marginTop: '40px'}}/>
            </CCardBody>
            <CCardFooter>
              <CRow className="text-center">
                <CCol md sm="12" className="mb-sm-2 mb-0">
                  <div className="text-muted">Total</div>
                  <strong>7.823 (40%)</strong>
                  <CProgress
                    className="progress-xs mt-2"
                    precision={1}
                    color="success"
                    value={40}
                  />
                </CCol>
                <CCol md sm="12" className="mb-sm-2 mb-0 d-md-down-none">
                  <div className="text-muted">Rata-rata</div>
                  <strong>200 (20%)</strong>
                  <CProgress
                    className="progress-xs mt-2"
                    precision={1}
                    color="info"
                    value={40}
                  />
                </CCol>
                <CCol md sm="12" className="mb-sm-2 mb-0">
                  <div className="text-muted">Tertinggi</div>
                  <strong>500 (60%)</strong>
                  <CProgress
                    className="progress-xs mt-2"
                    precision={1}
                    color="warning"
                    value={40}
                  />
                </CCol>
                <CCol md sm="12" className="mb-sm-2 mb-0">
                  <div className="text-muted">Terendah</div>
                  <strong>100 (80%)</strong>
                  <CProgress
                    className="progress-xs mt-2"
                    precision={1}
                    color="danger"
                    value={40}
                  />
                </CCol>
                <CCol md sm="12" className="mb-sm-2 mb-0 d-md-down-none">
                  <div className="text-muted">Modus</div>
                  <strong>300 (40.15%)</strong>
                  <CProgress
                    className="progress-xs mt-2"
                    precision={1}
                    value={40}
                  />
                </CCol>
              </CRow>
            </CCardFooter>
          </CCard>
    
          <CRow>
            <CCol>
              <CCard>
                <CCardHeader>
                  Pembatasan Sosial
                </CCardHeader>
                <CCardBody>
                  <CRow>
                    <CCol xs="12" md="6" xl="6">
    
                      <CRow>
                        <CCol sm="6">
                          <CCallout color="info">
                            <small className="text-muted">Sesuai aturan</small>
                            <br />
                            <strong className="h4">1.000</strong>
                          </CCallout>
                        </CCol>
                        <CCol sm="6">
                          <CCallout color="danger">
                            <small className="text-muted">Melanggar aturan</small>
                            <br />
                            <strong className="h4">1.823</strong>
                          </CCallout>
                        </CCol>
                      </CRow>
    
                      <hr className="mt-0" />
    
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                            Senin
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="34" />
                          <CProgress className="progress-xs" color="danger" value="78" />
                        </div>
                      </div>
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                          Selasa
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="56" />
                          <CProgress className="progress-xs" color="danger" value="94" />
                        </div>
                      </div>
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                          Rabu
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="12" />
                          <CProgress className="progress-xs" color="danger" value="67" />
                        </div>
                      </div>
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                          Kamis
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="43" />
                          <CProgress className="progress-xs" color="danger" value="91" />
                        </div>
                      </div>
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                          Jumat
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="22" />
                          <CProgress className="progress-xs" color="danger" value="73" />
                        </div>
                      </div>
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                          Sabtu
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="53" />
                          <CProgress className="progress-xs" color="danger" value="82" />
                        </div>
                      </div>
                      <div className="progress-group mb-4">
                        <div className="progress-group-prepend">
                          <span className="progress-group-text">
                          Minggu
                          </span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="info" value="9" />
                          <CProgress className="progress-xs" color="danger" value="69" />
                        </div>
                      </div>
                      <div className="legend text-center">
                        <small>
                          <sup className="px-1"><CBadge shape="pill" color="info">&nbsp;</CBadge></sup>
                          Sesuai aturan
                          &nbsp;
                          <sup className="px-1"><CBadge shape="pill" color="danger">&nbsp;</CBadge></sup>
                          Melanggar aturan
                        </small>
                      </div>
                    </CCol>
    
                    <CCol xs="12" md="6" xl="6">
    
                      <CRow>
                        <CCol sm="6">
                          <CCallout color="warning">
                            <small className="text-muted">Total Pengunjung</small>
                            <br />
                            <strong className="h4">2.823</strong>
                          </CCallout>
                        </CCol>
                        <CCol sm="6">
                          <CCallout color="success">
                            <small className="text-muted">Rata-rata</small>
                            <br />
                            <strong className="h4">400</strong>
                          </CCallout>
                        </CCol>
                      </CRow>
    
                      <hr className="mt-0" />
    
                      <div className="progress-group mb-4">
                        <div className="progress-group-header">
                          <CIcon className="progress-group-icon" name="cil-user" />
                          <span className="title">Laki-laki</span>
                          <span className="ml-auto font-weight-bold">43%</span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="warning" value="43" />
                        </div>
                      </div>
                      <div className="progress-group mb-5">
                        <div className="progress-group-header">
                          <CIcon className="progress-group-icon" name="cil-user-female" />
                          <span className="title">Perempuan</span>
                          <span className="ml-auto font-weight-bold">37%</span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="warning" value="37" />
                        </div>
                      </div>
                      <div className="progress-group">
                        <div className="progress-group-header">
                          <CIcon className="progress-group-icon" name="cil-globe-alt" />
                          <span className="title">Dewasa</span>
                          <span className="ml-auto font-weight-bold">1.823 <span className="text-muted small">(56%)</span></span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="success" value="56" />
                        </div>
                      </div>
    
    
                      <div className="progress-group">
                        <div className="progress-group-header">
                          <CIcon name="cib-facebook" className="progress-group-icon" />
                          <span className="title">Anak muda</span>
                          <span className="ml-auto font-weight-bold">500 <span className="text-muted small">(15%)</span></span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="success" value="15" />
                        </div>
                      </div>
                      <div className="progress-group">
                        <div className="progress-group-header">
                          <CIcon name="cib-twitter" className="progress-group-icon" />
                          <span className="title">Orang tua</span>
                          <span className="ml-auto font-weight-bold">300 <span className="text-muted small">(11%)</span></span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="success" value="11" />
                        </div>
                      </div>
                      <div className="progress-group">
                        <div className="progress-group-header">
                          <CIcon name="cib-linkedin" className="progress-group-icon" />
                          <span className="title">Bayi</span>
                          <span className="ml-auto font-weight-bold">200 <span className="text-muted small">(8%)</span></span>
                        </div>
                        <div className="progress-group-bars">
                          <CProgress className="progress-xs" color="success" value="8" />
                        </div>
                      </div>
                      <div className="divider text-center">
                        <CButton color="link" size="sm" className="text-muted">
                          <CIcon name="cil-options" />
                        </CButton>
                      </div>
    
                    </CCol>
                  </CRow>
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        </>
    );
}

export default view;